import React, { useState } from "react";

function Input({ addToDo }) {
  const [input, setInput] = useState("");
  return (
    <div>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          addToDo(input);
          setInput("");
        }}
      >
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
        />
        <input type="Submit" value="Add"></input>
      </form>
    </div>
  );
}

export default Input;
